import apiClient, { setAuthToken } from './apiClient';
import { setUsername } from './authService';

// 소셜 로그인 제공자 타입
export type SocialProvider = 'kakao' | 'naver' | 'google';

// 기본 URL - GitHub Pages 배포 URL
const BASE_URL = 'https://hyunki.github.io/TDC';
// 백엔드 URL
const BACKEND_URL = 'https://tdc-00qb.onrender.com';

// 개발 환경인지 확인
const isDevelopment = process.env.NODE_ENV === 'development';
const siteOrigin = isDevelopment ? window.location.origin : BASE_URL;

// 카카오 인증 관련 상수
const KAKAO_CLIENT_ID = '7a8ccc15d52d94a934242f9807ffe8ff';
const KAKAO_REDIRECT_URI = `${siteOrigin}/auth/kakao/callback`;
const KAKAO_AUTH_URL = `https://kauth.kakao.com/oauth/authorize?client_id=${KAKAO_CLIENT_ID}&redirect_uri=${KAKAO_REDIRECT_URI}&response_type=code`;

// 구글 인증 관련 상수
const GOOGLE_CLIENT_ID = '237309085107-lt0gshfjsjf0rtcbu9en2jm7kgmtki72.apps.googleusercontent.com';
const GOOGLE_REDIRECT_URI = `${siteOrigin}/auth/google/callback`;
const GOOGLE_AUTH_URL_BASE = 'https://accounts.google.com/o/oauth2/v2/auth';
const GOOGLE_AUTH_URL = `${GOOGLE_AUTH_URL_BASE}?client_id=${GOOGLE_CLIENT_ID}&redirect_uri=${GOOGLE_REDIRECT_URI}&response_type=code&scope=email%20profile`;

// 네이버 인증 관련 상수
const NAVER_CLIENT_ID = 'Rn94OZwT7iZYAiUEtU1T';
const NAVER_REDIRECT_URI = `${siteOrigin}/auth/naver/callback`;
const NAVER_AUTH_URL_BASE = 'https://nid.naver.com/oauth2.0/authorize';

// 소셜 로그인 응답 인터페이스
export interface SocialLoginResponse {
  success: boolean;
  message: string;
  token?: string;
  username?: string;
  socialId?: string;
  provider?: SocialProvider;
  redirectUrl?: string;
  profileImage?: string;
  nickname?: string;
}

// 소셜 인증 코드 요청 파라미터
export interface SocialAuthCodeParams {
  code: string;
  provider: SocialProvider;
}

/**
 * 카카오 로그인 링크 반환
 */
export const getKakaoLoginUrl = async (): Promise<string> => {
  try {
    // 백엔드에서 클라이언트 ID와 리디렉션 URI 가져오기
    const response = await apiClient.get('/api/auth/kakao-info');
    const { clientId, redirectUri } = response.data;
    
    // 카카오 로그인 URL 생성
    const kakaoParams = new URLSearchParams({
      client_id: clientId,
      redirect_uri: redirectUri,
      response_type: 'code'
    });
    
    return `https://kauth.kakao.com/oauth/authorize?${kakaoParams.toString()}`;
  } catch (error) {
    console.error('카카오 로그인 URL 생성 중 오류 발생:', error);
    // 오류 발생 시 하드코딩된 URL 사용 (폴백)
    return KAKAO_AUTH_URL;
  }
};

/**
 * 구글 로그인 URL 생성
 */
export const getGoogleLoginUrl = async (): Promise<string> => {
  try {
    // 백엔드에서 클라이언트 ID와 리디렉션 URI 가져오기
    const response = await apiClient.get('/api/auth/google-info');
    const { clientId, redirectUri } = response.data;
    
    // 구글 로그인 URL 생성
    const googleParams = new URLSearchParams({
      client_id: clientId,
      redirect_uri: redirectUri,
      response_type: 'code',
      scope: 'email profile',
      access_type: 'offline',
      prompt: 'consent'
    });
    
    return `${GOOGLE_AUTH_URL_BASE}?${googleParams.toString()}`;
  } catch (error) {
    console.error('구글 로그인 URL 생성 중 오류 발생:', error);
    return GOOGLE_AUTH_URL; // 폴백 URL 사용
  }
};

/**
 * 네이버 로그인 URL 생성
 */
export const getNaverLoginUrl = async (): Promise<string> => {
  try {
    // 백엔드에서 클라이언트 ID와 리디렉션 URI 가져오기
    const response = await apiClient.get('/api/auth/naver-info');
    const { clientId, redirectUri } = response.data;
    
    // 무작위 상태 값 생성 (CSRF 방지) - 더 강화된 방식
    const randomState = Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
    
    // 상태값을 로컬스토리지에 저장
    localStorage.removeItem('naverOAuthState'); // 기존 값 제거
    localStorage.setItem('naverOAuthState', randomState);
    
    // 세션 스토리지에도 중복 저장 (안전성 강화)
    sessionStorage.setItem('naverOAuthState', randomState);
    
    console.log('네이버 로그인 상태값 생성:', randomState);
    
    // 네이버 로그인 URL 생성
    const naverParams = new URLSearchParams({
      response_type: 'code',
      client_id: clientId,
      redirect_uri: redirectUri,
      state: randomState
    });
    
    return `${NAVER_AUTH_URL_BASE}?${naverParams.toString()}`;
  } catch (error) {
    console.error('네이버 로그인 URL 생성 중 오류 발생:', error);
    
    // 오류 발생 시 하드코딩된 URL 사용 (폴백)
    const randomState = Math.random().toString(36).substring(2, 15);
    localStorage.setItem('naverOAuthState', randomState);
    sessionStorage.setItem('naverOAuthState', randomState);
    
    const naverParams = new URLSearchParams({
      response_type: 'code',
      client_id: NAVER_CLIENT_ID,
      redirect_uri: NAVER_REDIRECT_URI,
      state: randomState
    });
    
    return `${NAVER_AUTH_URL_BASE}?${naverParams.toString()}`;
  }
};

/**
 * 소셜 인증 코드를 백엔드로 전송하여 로그인 처리
 */
export const loginWithSocialAuthCode = async (
  params: SocialAuthCodeParams
): Promise<SocialLoginResponse> => {
  try {
    console.log('소셜 로그인 요청 시작:', params.provider);
    
    const response = await apiClient.post<SocialLoginResponse>(
      '/api/auth/social-login',
      params
    );
    
    console.log('소셜 로그인 응답:', response.data);
    
    // 로그인 성공 시 토큰과 사용자 이름 저장
    if (response.data.success && response.data.token) {
      console.log('토큰 저장 시작:', !!response.data.token);
      
      try {
        // 먼저 기존 토큰 제거
        localStorage.removeItem('token');
        sessionStorage.removeItem('token');
        
        // 로컬 스토리지에 토큰 저장
        localStorage.setItem('token', response.data.token);
        
        // 세션 스토리지에도 저장
        sessionStorage.setItem('token', response.data.token);
        
        // API 클라이언트에도 설정
        setAuthToken(response.data.token);
        
        // 토큰 저장 확인
        const savedToken = localStorage.getItem('token');
        console.log('저장된 토큰 확인:', !!savedToken);
        
        if (!savedToken) {
          console.error('토큰 저장 실패. 마지막 시도로 다시 저장합니다.');
          localStorage.setItem('token', response.data.token);
        }
        
        // 사용자 정보 저장
        if (response.data.username) {
          console.log('사용자 이름 저장:', response.data.username);
          localStorage.setItem('username', response.data.username);
          setUsername(response.data.username);
          
          // 사용자 상태를 ONLINE으로 설정
          try {
            await apiClient.post('/api/users/status', { 
              username: response.data.username, 
              status: 'ONLINE' 
            });
            console.log(`사용자 ${response.data.username} 상태를 ONLINE으로 변경했습니다.`);
          } catch (statusError) {
            console.error('소셜 로그인 후 사용자 상태 업데이트 오류:', statusError);
          }
        }
        
        // 닉네임 저장 (백엔드에서 제공하는 경우)
        if (response.data.nickname) {
          console.log('닉네임 저장:', response.data.nickname);
          localStorage.setItem('userNickname', response.data.nickname);
        }
      } catch (storageError) {
        console.error('토큰 저장 중 오류 발생:', storageError);
      }
    } else {
      console.log('소셜 로그인 실패:', response.data.message);
    }
    
    return response.data;
  } catch (error: any) {
    console.error('소셜 로그인 중 에러 발생:', error);
    return {
      success: false,
      message: '서버 연결 중 오류 발생'
    };
  }
};

// 카카오 로그인 상태 확인
export const checkKakaoLoginStatus = (): boolean => {
  // 브라우저에서 실행 중인지 확인
  if (typeof window === 'undefined') return false;
  
  // URL에서 인증 코드 확인
  const params = new URLSearchParams(window.location.search);
  const code = params.get('code');
  
  return Boolean(code);
}; 